.home {
    display: flex;

    .homeContainer {
        flex: 6;

        .widgets,
        .charts {
            display: flex;
            padding: 20px;
            gap    : 20px;
        }

        .charts {
            padding: 5px 20px;
        }

        .listContainer {
            padding   : 20px;
            margin    : 20px;
            box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);

            .listTitle {
                font-weight  : 500;
                color        : gray;
                margin-bottom: 15px;
            }
        }
    }
}